<template>
  <div class="invite">
    <img src="@/assets/images/my/bg1.png" alt="" style="width:80%;margin:30px auto;">
    <div class="code">
      <h1 class="green">关注公众号体验更多精彩内容</h1>
      <img class="codeImg" src="@/assets/images/my/nologin.jpg" />
      <p class="fff">长按识别二维码</p>
      <p class="fff">关注公众号进行扫码充电</p>
    </div>
    <audio id="mp3">
      <source src="@/assets/images/my/welcome.mp3" type="audio/ogg" />
      <source src="@/assets/images/my/welcome.mp3" type="audio/mpeg" />
    </audio>
  </div>
</template>


<script>
import { yaoqingma } from "@/axios/getData";
export default {
  data() {
    return {
      img: '',
      aa: false
    };
  },
  mounted(){
    var voice = document.getElementById('mp3');
    document.addEventListener("touchstart",function(e){
      voice.play();
    }, false );
  },
  created() {
    this.getData();
  },
  methods: {
    async getData() {
      var res = await yaoqingma();
      this.img = res.data.ewm
    }
  }
};
</script>


<style scoped lang="scss">
.fff{
  color:rgb(26, 160, 52);
  font-size: 40px;
  font-weight: 600;
}
.invite {
  padding:1px 0;
  background: -webkit-linear-gradient(bottom,#011957,#010d32);
  height: 100vh;
  .code {
    margin: 0 100px;
    background: rgba(255, 255, 255, 1);
    border-radius: 13px;
    display: flex;
    align-items: center;
    flex-direction: column;
    padding: 20px 0;
    margin-bottom:80px;
    h1 {
      font-size: 18px;
    }
    .codeImg {
      height: 450px;
      width: 450px;
    }
  }
  .bot {
    display: flex;
    justify-content: space-around;
    color: #fff;
    margin-top: 30px;
    .flex {
      display: flex;
      flex-direction: column;
      align-items: center;
    }
    img {
      width: 30px;
      height: 30px;
      margin-bottom: 10px;
    }
  }
}
.green{
  color:rgb(26, 160, 52);
}
</style>



